  .burger__open.active:before {
    background-color: #fff;
    top: 9px;
    transform: rotate(45deg);
  }

  .burger__open.active:after {
    background-color: #fff;
    bottom: 9px;
    transform: rotate(-45deg);
  }

  .burger__open.active span {
    transform: scale(0);
